<template>
  <div>
    <!-- Using button-content slot -->
    <b-nav-item-dropdown right v-if="loggedIn">
        <template slot="button-content">
          <em>{{ username }}</em>
        </template>
        <b-dropdown-item href="#">Profile</b-dropdown-item>
        <b-dropdown-item href="/api/api-auth/logout/?next=/portal_vue/#">Signout</b-dropdown-item>
    </b-nav-item-dropdown>
    <div v-else>
      <a href="/api/api-auth/login/?next=/portal_vue/#">Login</a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'LoginStatus',
  data () {
    return {
      loggedIn: false,
      username: '',
      firstname: '',
      lastname: ''
    }
  },
  mounted () {
    this.getUserDetails()
  },
  methods: {
    getUserDetails () {
      fetch('/api/user/?format=json', {credentials: 'include'})
        .then(response => {
          return response.json() // we only get here if there is no error
        })
        .then(json => {
          this.loggedIn = true
          this.username = json.username
          this.firstname = json.firstname
          this.lastname = json.lastname
        })
        .catch(err => {
          console.log(err)
          this.loggedIn = false
          this.username = ''
          this.firstname = ''
          this.lastname = ''
        })
    }
  }
}
</script>



