<template>
  <div id="app">
    <b-container fluid>
    <b-navbar toggleable="md" type="dark" variant="info">
      <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>

      <b-navbar-brand href="#">{{ brand }}</b-navbar-brand>

      <b-collapse is-nav id="nav_collapse">

        <!-- Right aligned nav items -->
        <b-navbar-nav class="ml-auto">
          <LoginStatus></LoginStatus>
        </b-navbar-nav>

      </b-collapse>

    </b-navbar>
    <b-card no-body class="mh-100">
      <b-tabs card>
        <b-tab title="About" active class="mh-100">
          <About></About>
        </b-tab>
        <b-tab title="Map" @click="resizeMap" style="height: 500px;">
          <Map ref="Map"></Map>
        </b-tab>
        <b-tab title="My ETAG Data">
          <Custom></Custom>
        </b-tab>
      </b-tabs>
    </b-card>
    </b-container>
  </div>
</template>

<script>
import Status from './components/Status'
import About from './components/About'
import Map from './components/Map'
import Custom from './components/Custom'
import LoginStatus from './components/LoginStatus'
export default {
  name: 'App',
  components: {About, Status, Map, Custom, LoginStatus},
  data () {
    return {
      brand: 'Electronic Transponder Analysis Gateway Data Portal'
    }
  },
  methods: {
    resizeMap () {
      // This is a fix to redraw the leaflet map
      setTimeout(() => {
        this.$refs.Map.resizeMap()
      }, 0)
    }
  }
}
</script>

<style>
  /* @import './assets/style.css'; */
  @import './assets/bootstrap.min.css';
  @import "../node_modules/leaflet/dist/leaflet.css";
</style>



